import Vue from 'vue'
import Router from 'vue-router'
import step1 from '@/pages/step1'
import step2 from '@/pages/step2'
import step3 from '@/pages/step3'
import step4 from '@/pages/step4'
import step5 from '@/pages/step5'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/f2e/week6/',
      name: 'Home',
      redirect: '/f2e/week6/step1'
    },
    {
      path: '/f2e/week6/step1',
      name: 'step1',
      component: step1
    },
    {
      path: '/f2e/week6/step2',
      name: 'step2',
      component: step2
    },
    {
      path: '/f2e/week6/step3',
      name: 'step3',
      component: step3
    },
    {
      path: '/f2e/week6/step4',
      name: 'step4',
      component: step4
    },
    {
      path: '/f2e/week6/step5',
      name: 'step5',
      component: step5
    }
  ]
})



